import React, { useState, useEffect, useRef } from "react";
import styledComponents from "styled-components";

const Container = styledComponents.div`
    width: 55%;
    overflow: hidden; //      .
`;
const Button = styledComponents.button`
    all: unset;
    margin-top: 20px;
    margin-right: 100px;
    margin-left: 100px;
    border: 1px solid white;
    padding: 0.5em 2em;
    color: white;
    border-radius: 10px;
    &:hover {
        transition: all 0.3s ease-in-out;
        background-color: gray;
        color: #fff;
    }
`;
const SliderContainer = styledComponents.div`
    width: 100%;
    display: flex; //  .
`;

const IMG = styledComponents.img`
    width: 100%;
    height: 50vh;
`;

const Sliderworldinfo = (props) => {

    const TOTAL_SLIDES = 4;

    const [currentSlide, setCurrentSlide] = useState(0);

    const slideRef = useRef(null);

    const nextSlide = () => {
        if (currentSlide >= TOTAL_SLIDES) { //       .
            setCurrentSlide(0);
        } 
        else {
            setCurrentSlide(currentSlide + 1);
        }
    }

    const prevSlide = () => {
        if (currentSlide === 0) {
            setCurrentSlide(TOTAL_SLIDES);
        } 
        else {
            setCurrentSlide(currentSlide - 1);
        }
    }

    useEffect(() => {
        slideRef.current.style.transition = "all 0.5s ease-in-out";
        slideRef.current.style.transform = `translateX(-${currentSlide}00%)`; //      .
        }, [currentSlide]);

    return (
        <Container>
            {/* {currentSlide} */}
            <SliderContainer ref={slideRef}>
                <IMG src={props.imgs[0].img1} alt=" ."/>
                <IMG src={props.imgs[0].img2} alt=" ."/>
                <IMG src={props.imgs[0].img3} alt=" ."/>
                <IMG src={props.imgs[0].img4} alt=" ."/>
                <IMG src={props.imgs[0].img5} alt=" ."/>
            </SliderContainer>
            <Button onClick={prevSlide}>Prev</Button>
            <Button onClick={nextSlide}>Next</Button>
        </Container>
    )
}

export default Sliderworldinfo;